<template>
    <section>
        <section class="layout-waybill" v-if="hasWaybill">
            <div class="header">
                <p class="title">
                    #{{waybill.numberInvoice}}
                </p>

                <section class="edit-wrapper" v-if="waybill.isEditable">
                    <span v-show="!editing.isEditing" v-on:click="editHandler">редактировать</span>

                    <img v-show="editing.isEditing" class="icon" src="/svgs/check.svg" alt="сохранить изменения"/>
                    <img v-show="editing.isEditing" v-on:click="editHandler" class="icon"
                         src="/svgs/times.svg" alt="отменить изменения"/>
                </section>

                <p class="delivery-date" v-if="waybill.deliveryDate">{{waybill.deliveryDate}}</p>
                <p class="delivery-date" v-else>Резерв</p>
            </div>

            <div class="content">

                <ProductsTable  @quantityChange="onQuantityChange" @remove="onRemove"
                                v-bind:data="waybill.products" :has-numbering="true"
                                v-bind:is-editable="editing.isEditing">
                </ProductsTable>

                <MDRaisedButton v-if="editing.isEditing" text="Добавить Товар" v-on:click="onAddButtonClick" />

                <MDInput class="comment-min" label="Комментарий" v-if="editing.isEditing">
                </MDInput>
                <p class="comment" v-if="!editing.isEditing && waybill.comment">Примечание: {{waybill.comment}}</p>
                <p class="comment" v-if="!editing.isEditing && !waybill.comment">Нету примечания</p>

                <div class="sub-items">
                    <p class="is-paid is-paid-true" v-if="waybill.isPaid">Оплачен</p>
                    <p class="is-paid is-paid-false" v-if="!waybill.isPaid">Не оплачен</p>
                    <p class="total-price">Всего:
                        <span class="bold" v-if="!editing.isEditing">{{waybill.totalPrice}}₴</span>
                        <span class="bold" v-if="editing.isEditing">{{calculatedTotalPrice}}₴</span>
                    </p>
                </div>

            </div>

        </section>

        <section class="center">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </section>

        <p v-if="!hasWaybill && !isLoading" class="error">Накладная с номером: {{id}}. Не найдена</p>

        <WaybillEditingDialog v-if="editing.isEditing" v-bind:products-data="waybill.products"  ref="dialogWrapper"/>
    </section>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex';
    import modules from '../store/all.modules.name';
    import actions from "../store/all.actions.type";

    import ProductsTable from "./components/ProductsTable";
    import MDRaisedButton from "./components/MD/MDRaisedButton";
    import MDInput from "./components/MD/MDInput";
    import LoadingSpinner from "./components/LoadingSpinner";

    export default {
        name: "Waybill",

        components: {
            MDRaisedButton,
            MDInput,
            LoadingSpinner,
            ProductsTable,
            WaybillEditingDialog: () => import("./components/WaybillEditingDialog"),
        },

        data: () => {
            return {
                waybill: null,
                waybillCopy: null,

                editing: {
                    isEditing: false,
                }
            }
        },

        methods: {
            ...mapActions(modules.waybills, [
                actions.waybills.GET_EXTENDED_WAYBILL,
            ]),

            loadWaybill() {
                this.getExtendedWaybill(this.id).then(res => {
                    this.waybill = res;
                })
            },

            cancelAllChanges() {
                this.waybill = Object.assign({}, this.waybillCopy);
                this.waybillCopy = null;
            },

            editHandler() {
                this.editing.isEditing = !this.editing.isEditing;

                 if (this.editing.isEditing === false) {
                     this.cancelAllChanges();
                } else {
                     this.waybillCopy = JSON.parse(JSON.stringify(this.waybill));
                     this.waybill.products.forEach((item) => {
                         item.isValid = true;
                     });
                }
            },

            onAddButtonClick() {
                this.$refs.dialogWrapper.$refs.dialog.show();
                setTimeout(() => {
                    this.$refs.dialogWrapper.$refs.searchInput.focus();
                }, 500);
            },

            onRemove(index) {
                if (this.waybill.products[index]) {
                    this.waybill.products.splice(index, 1);
                }
            },

            onQuantityChange(payload) {
                let {item, newQuantity} = payload;
                item.boughtQuantity = newQuantity;
            }
        },

        computed: {
            ...mapGetters(modules.waybills, [
                'isLoading'
            ]),

            calculatedTotalPrice: function() {
                let res = 0;
                this.waybill.products.forEach((item) => {
                    res += item.boughtQuantity * item.price;
                });
                return Math.round(res * 1000) / 1000;
            },

            id: function () {
                if (this.$route && this.$route.params) {
                    return this.$route.params.id;
                } else {
                    return null;
                }
            },

            hasId: function () {
                return this.id !== null
            },

            hasWaybill: function () {
                return this.waybill !== null
            },
        },

        mounted() {
            if (this.hasId) {
                this.loadWaybill();
            }
        }
    }
</script>

<style lang="sass" scoped>

    .center
        display: flex
        justify-content: center
        height: 100px

    .error
        text-align: center
        font-family: Roboto, sans-serif
        font-size: 1.5rem

    .layout-waybill
        width: 90%
        max-width: 600px
        margin: auto
        font-family: Roboto, sans-serif
        box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.22)
        padding: 25px

        .header
            margin-bottom: 2rem

            p
                text-align: center

            .edit-wrapper
                display: flex
                justify-content: center

                .icon
                    width: 16px
                    height: 16px
                    margin-left: 10px
                    cursor: pointer
                    font-weight: normal

                span
                    font-weight: bold
                    font-size: 14px
                    color: #1d5db5
                    cursor: pointer
                    text-align: center
                    margin-left: auto
                    margin-right: auto
                    padding: 5px
                    border-radius: 5px
                    transition: 0.35s

                    &:hover
                        background-color: #1d5db524

            .title
                font-weight: bold
                font-size: 18px

            .delivery-date
                color: #777777
                font-size: 15px

        .content

            table
                width: 100%
                border-collapse: collapse
                border-top: 2px solid #d0d0d0

                tr
                    &:nth-child(even)
                        background-color: #f2f2f2

                th, td
                    transition: 0.5s
                    text-align: left
                    padding: 16px 8px

            button
                margin-top: 10px
                margin-left: auto
                margin-right: auto

            .comment
                margin-top: 2rem
                margin-bottom: 0

            .comment-min
                margin-top: 1rem
                margin-bottom: 0

            .sub-items
                margin-top: 0.25rem
                display: flex

                .bold
                    font-weight: bold

                .total-price
                    margin-left: auto

                .is-paid-true
                    color: #81C784

                .is-paid-false
                    color: #e57373
</style>